import React, {Component} from 'react';
import {Card, CardBody, CardHeader, CardTitle, Col, Input, Nav, Progress, Row} from 'reactstrap';
import 'react-table/react-table.css'
import Notify from 'react-notification-alert';
import 'react-datetime/css/react-datetime.css'
import 'moment/locale/tr'
import HsmErrorAlert from '../../../common/hsm/HsmErrorAlert'
import ReactLoading from 'react-loading';
import StickButton from "../../../components/StickButton";
import {requestK} from "../../../helper/APIUtils";
import {checkGrantToDisplay, showAxiosError} from "../../../helper/helper";
import SweetAlert from "react-bootstrap-sweetalert";
import Alert from 'react-s-alert';
import {FORM_SEND, HSM_AUTHORITY, ISG_FORM_SEND, HEALTH_FORM_SEND, ZIMMET_FORM_SEND, KVKK_FORM_SEND} from "../../../common/authorities/GrantDef";

class SignFormHsmPage extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      completed: false,
      signedDocumentCount: 0,
      progressLength: 1,
      pageStatus: 0,
      timer: 1,
      timerStarted: false,
      certError: true,
      selectedIdList: [],
      disableButton: false
    };

    this.sign = this.sign.bind(this);
    this.signDocuments = this.signDocuments.bind(this);
    this.updateMimeInfo = this.updateMimeInfo.bind(this);

    this.stateMap = {
      0: {
        label: "Pdf dosyaları imzalama için hazır. İmzalayınız",
      },
      10: {
        label: "İletiler oluşturuluyor. Lütfen bekleyiniz.",
        wait: true
      },
      30: {
        label: "Pdf dosyaları imzalanıyor. Lütfen bekleyiniz",
      },
      // İmzalanamadı ise;
      40: {
        label: "Pdf dosyaları imzalanamadı. Lütfen bilgileri kontrol ediniz.",
        danger: true
      },
      50: {
        label: "Pdf dosyaları imzalandı."
      },
      60: {
        label: ""
      },
    };
  }

  componentWillMount() {
    this.setState({
      formSessionId: this.props.location.formSessionId,
      kepAccountList: this.props.location.kepAccountList,
      mimeTitle: this.props.location.mimeTitle,
      mimeBody: this.props.location.mimeBody,
      selectedIdList: this.props.location.selectedIdList
    });
    this.readSmsTemplate();
  }

  readSmsTemplate = () => {
    let self = this;
    let params = {
      url: `/signer/readFormSmsTemplateInfo`,
      method: "get"
    };
    requestK(params)
      .then(function (response) {
        self.setState({smsBody: response.data})
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  componentDidUpdate() {
    if (this.state.timer === 1 && !this.state.timerStarted &&
      this.state.completed && this.state.signedDocumentCount === this.state.progressLength) {
      setInterval(() => {
        this.setState(prevState => ({timer: prevState.timer - 1}))
      }, 4000);
      this.setState({timerStarted: true});
    } else if (this.state.timer === 0) {
      this.setState({backToTheFuture: true});
    }
    if (this.state.backToTheFuture) {
      this.props.history.push({pathname: `/${this.props.peykName}/formsession/` + this.state.formSessionId});
    }
  }

  isNoTemplateValid = () => {
    if (this.state.mimeTitle.trim() === "") {
      Alert.error("E-Posta İleti Konusu alanı boş bırakılamaz...", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
      this.setState({alert: null});
      return false;
    } else if (this.state.mimeBody.trim() === "") {
      Alert.error("E-Posta İleti İçeriği alanı boş bırakılamaz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
      this.setState({alert: null});
      return false;
    } else if (this.state.smsBody.trim() === "") {
      Alert.error("SMS İleti İçeriği alanı boş bırakılamaz.", {
        position: 'top-right',
        effect: 'stackslide',
        timeout: 5000
      });
      this.setState({alert: null});
      return false;
    }else{
      return true;
    }
  }
  sign() {
    if(this.isNoTemplateValid()){
      this.updateMimeInfo();
    }
  }

  updateMimeInfo() {
    let self = this;
    let params = {
      url: `/signer/writeFormMimeInfo`,
      method: "post",
      data: {
        formSessionId: parseInt(self.props.location.formSessionId),
        title: self.state.mimeTitle,
        body: self.state.mimeBody,
        smsBody: self.state.smsBody
      },
      timeout: 9600000
    };
    self.setState({pageStatus: 10, alert: null, disableButton: true});
    requestK(params)
      .then(function (response) {
        self.signDocuments()
      })
      .catch(function (error) {
        showAxiosError(error);
        self.setState({pageStatus: 40, disableButton: false});
      });
  }

  timerStatus(status){
    this.props.onTimerStatus(status);
  };
  signDocuments() {
    this.setState({
      progressLength: this.state.selectedIdList.length,
      loading: true,
      pageStatus: 30
    });
    this.timerStatus(true);
    requestK({
      url: `/signer/hsm-sign-form-list`,
      method: "post",
      data: this.state.selectedIdList
    }).then((response) => {
      Alert.success(response.data);
      this.stateMap[60].label = response.data;
      this.setState({
        loading: false,
        completed: true,
        pageStatus: 60
      });
      setInterval(() => {
        this.setState({backToTheFuture: true})
      }, 2000);
    }).catch(error => {
      this.setState({
        loading: false,
        completed: true,
        pageStatus: 40,
        disableButton: false,
        alert: <HsmErrorAlert error={error} onConfirm={() => this.setState({alert: null})}/>
      });
      this.timerStatus(false);
    });
  }

  renderNavButtons() {
    return (
      <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
        <StickButton position="leftSide" className="zocial GeriDon" style={{fontSize: 'small'}}
                     label="Geri" title="Önceki Sayfaya Git"
                     onClick={() => this.props.history.push({
                       pathname: `/${this.props.peykName}/formsession/${this.state.formSessionId}`
                     })}/>
        {((checkGrantToDisplay(this, FORM_SEND) || checkGrantToDisplay(this, HSM_AUTHORITY)) || checkGrantToDisplay(this,ISG_FORM_SEND) || checkGrantToDisplay(this,HEALTH_FORM_SEND) || checkGrantToDisplay(this,ZIMMET_FORM_SEND) || checkGrantToDisplay(this,KVKK_FORM_SEND)) &&
        <StickButton position="leftSide" className="zocial Imza" style={{fontSize: 'small'}}
                     label="İmzala" title="Formları İmzala"
                     disabled={this.state.disableButton}
                     onClick={() => {
                       this.setState({
                         alert: (
                           <SweetAlert showCancel warning
                                       confirmBtnText="Evet"
                                       cancelBtnText="Hayır"
                                       confirmBtnBsStyle="success"
                                       cancelBtnBsStyle="default"
                                       title="Emin Misiniz?"
                                       onConfirm={() => this.sign()}
                                       onCancel={() => this.setState({alert: null})}
                           >
                             Dokümanlar imzalanıp Tübitak zaman damgası ile mühürlenerek gönderilecektir.
                           </SweetAlert>
                         )
                       });
                     }}/>}
      </Nav>
    );
  }

  renderState() {
    let stateMap = this.stateMap[this.state.pageStatus];

    return <div className="row" style={{marginBottom: "15px"}}>
      <Col sm={12}>
        <div style={{display: "inline-block", marginRight: "8px"}}>
          {stateMap.wait && <ReactLoading type="balls" color="#2c2c2c" height={24} width={24}/>}
        </div>
        <div style={{
          display: "inline-block", marginRight: "8px", position: "relative", top: "-6px",
          color: stateMap.danger === true ? "trueColor" : "falseColor"
        }}>
          {stateMap.label}
        </div>
      </Col>
    </div>
  }

  renderMimeInputs() {

    return <div className="FormRenk">
      <Row sm={6}>
        <Col sm={2} style={{top: '5px'}}>
          E-Posta Konu
        </Col>
        <Col sm={6}>
          <Input type="text" style={{marginTop: '2px'}}
                 value={this.state.mimeTitle}
                 onChange={(e) => this.setState({mimeTitle: e.target.value})}/>
        </Col>
      </Row>
      <Row sm={6}>
        <Col sm={2} style={{top: '25px'}}>
          E-Posta İçerik
        </Col>
        <Col sm={6}>
          <Input type="textarea" rows={5}
                 style={{
                   height: '200px',
                   border: '1px solid #E3E3E3',
                   marginTop: '2px',
                   padding: '3px 3px'
                 }}
                 value={this.state.mimeBody}
                 onChange={(e) => this.setState({mimeBody: e.target.value})}/>
        </Col>
      </Row>
      <Row sm={6}>
        <Col sm={2} style={{top: '25px'}}>
          SMS İçerik
        </Col>
        <Col sm={6}>
          <Input type="textarea" rows={5}
                 style={{
                   height: '200px',
                   border: '1px solid #E3E3E3',
                   marginTop: '2px',
                   padding: '3px 3px'
                 }}
                 value={this.state.smsBody}
                 onChange={(e) => this.setState({smsBody: e.target.value})}/>
        </Col>
      </Row>
    </div>
  }

  renderSignProgress() {
    let percentage = this.state.signedDocumentCount * 100 / this.state.progressLength;
    let textList = this.state.pdfName && this.state.pdfName.split("#");

    return <div className="FormRenk" style={{paddingRight: '35px'}}>
      <div className="component-fluid">
        <div className="row">
          <div className="col-sm-12">
            İmzalanan dosya:
          </div>
          <div className="col-sm-12">
            {this.state.pdfName &&
            <div style={{display: "inline-block", marginRight: "8px"}}>
              <span>{textList[0]}</span>
              <span style={{fontWeight: 'bold'}}>{textList[1]}</span>
              <span>{textList[2]}</span>
              <span style={{fontWeight: 'bold'}}>{textList[3]}</span>
              <div style={{
                display: "inline-block",
                marginRight: "8px",
                position: 'absolute',
                top: '5px',
                paddingLeft: '2px'
              }}>
                <ReactLoading type="balls" color="#2c2c2c" height={12} width={12}/>
              </div>
            </div>}
          </div>
          <div className="col-sm-12">
            <Progress value={percentage}>{"%" + Math.round(percentage)}</Progress>
          </div>
        </div>
      </div>
    </div>;
  }

  render() {
    return (
      <Row className="baseBackground">
        <Notify ref="notify"/>
        {this.state.alert}
        {this.renderNavButtons()}
        <div className="row satirBasi">
          <Col md="12">
            <Card className="card">
              <CardHeader>
                <CardTitle tag="h4">
                  Form İmzala
                </CardTitle>
              </CardHeader>
              <CardBody>
                {this.state.formSessionId && this.renderState()}
              </CardBody>
              <CardBody>
                {this.state.formSessionId && this.renderMimeInputs()}
              </CardBody>
              <CardBody>
                {this.state.loading && this.renderSignProgress()}
              </CardBody>
            </Card>
          </Col>
        </div>
      </Row>

    );
  }
}

export default SignFormHsmPage;
